#main {
    margin: 2rem 14rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1rem;
}

@media screen and (max-width: 1000px) {
    #main {
        margin: 2rem 6rem;
    }
}

@media screen and (max-width: 600px) {
    #main {
        margin: 2rem .5rem;
    }
}

